@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
html  {
    height: 100%;
}
body {
    display: flex;
    justify-content: center;
    margin: 0;
    font-family: "Open Sans", sans-serif;
    height: 100%;
}

ul {
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

.container {
    min-height: 100%;
    background-color: #E5E5E5;
    width: 960px;
    padding-top: 50px;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-content: flex-start;
}

.row {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.row_btwn {
    justify-content: space-between;
    margin-bottom: 20px;
}

[class*='col-'] {
    box-sizing: border-box;
    margin-right: -5px;
    margin-left: -5px;
}

[class*='col-side'] {
    width: 25%;
    min-width: 240px;
}

[class*='col-main'] {
    width: 50%;
    min-width: 500px;
}

.card {
    background: #FFFFFF;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 20px;
    margin: 10px;
}

[class*="text-grey"] {
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #A0B0B9;
}

[class*="fw-bold"] {
    font-size: 14px;
    line-height: 21px;
    color: #4A4A4A;
}


/* рассинхрон с SASS */

@media (max-width: 959px) {
    .container {
        width: 100%;
    }
    .row {
        flex-flow: row wrap;
    }
}

@media (max-width: 501px) {
    [class*='col-main'] {
        width: 100%;
        min-width: -webkit-fit-content;
        min-width: -moz-fit-content;
        min-width: fit-content;
    }
}
.timeout {
  position: fixed;
  top: 0;
  height: 1em;
  background-color: #2196F3;
  width: 20%;
  opacity: 0;
  transition: .5s;
}
.timeout_run {
  opacity: 1;
  width: 100%;
  transition: 2s;
}
.timeout__tip {
  color: #E5E5E5;
  font-weight: 400;
  font-size: 0.9em;
  line-height: 1em;
  font-style: italic;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  z-index: 1000;
  opacity: 0;
}
.timeout__tip_run {
  opacity: 1;
  transition: 0.5s;
}
.filter {
  display: flex;
  flex-flow: column wrap;
  align-content: flex-start;
  justify-content: flex-start;
}

.form__header {
  font-weight: 600;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #4A4A4A;
  text-align: left;
  margin-bottom: 20px;
}

.filter__item {
  height: 40px;
  width: 100%;
}

.filter__label {
  font-size: 13px;
  width: 100%;
  height: 100%;
}

.filter__input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.filter__input + label {
  display: inline-flex;
  align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.filter__input + label::before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #adb5bd;
  border-radius: 0.25em;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  transition: 0.5s;
}

.filter__input:checked + label::before {
  border: 1px solid #2196F3;
  background-image: url(/test-task_aviasales/static/media/checkmark.68ca57dc.svg);
}
/* рассинхрон с SASS */
.buffered + label::before {
  background-color:#a2d5ff;
}

.filter__input:not(:disabled):not(:checked) + label:hover::before {
  border-color: #2196F3;
}

.filter__input:not(:disabled):checked + label:hover::before {
  border-color: #adb5bd;
  background-size: 75% 75%;
}

.filter__input:focus:not(:checked) + label::before {
  border-color: #80bdff;
}

.filter__input:disabled + label::before {
  background-color: #e9ecef;
}
.tabs {
  padding: 0;
  display: flex;
  background-color: rgba(255, 255, 255, 0);
  box-shadow: none;
}

.tabs__item {
  cursor: pointer;
  display: block;
  line-height: 50px;
  width: 50%;
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  background-color: #fff;
  color: #000000;
  border: 1px solid #DFE5EC;
  box-sizing: border-box;
}

.tabs__item_active {
  background-color: #2196F3;
  color: #FFFFFF;
  cursor: default;
}

.tabs__item:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.tabs__item:last-child {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.price {
    -ms-grid-row-align: center;
    align-self: center;
}

.price {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 24px;
    color: #2196F3;
    text-transform: uppercase;
}

.logo-iata {
    height: 36px;
}

.flight {
    height: 39px;
    margin-bottom: 10px;
}

.flight:last-child {
    margin-bottom: 0;
}

.flight__block {
    display: flex;
    flex-flow: column wrap;
    font-weight: 600;
    width: 140px;
}

.flight__block:last-child {
    margin-left: 20px;
}

.ticket {
    display: flex;
    flex-flow: column;
}

@media (max-width: 500px) {
    .ticket {
        padding: 10px;
        /* margin: 10px 0; */
    }
    .ticket {
        flex-flow: row wrap;
    }
    .flight__block {
        min-width: 100px;
        width: 33%;
    }
    .flight__block:last-child {
        margin-left: 0;
    }
}

@media (max-width: 330px) {
    .ticket .flight {
        flex-flow: column nowrap;
        width: 50%;
        height: 100%;
    }
}
.preloader__wrapper {
  display: flex;
  flex-direction: column !important;
  align-content: center;
  align-items: center;
}
.preloader__descr {
  color: #8f8f8f !important;
}
.cssload-container {
	width: 100%;
	height: 49px;
	text-align: center;
}
.cssload-speeding-wheel {
	width: 49px;
	height: 49px;
	margin: 0 auto;
	border: 1px solid #2196F3;
	border-radius: 100%;
	border-left-color: transparent;
	border-right-color: transparent;
	-webkit-animation: cssload-spin 0.5s infinite linear;
	        animation: cssload-spin 0.5s infinite linear;
}
@-webkit-keyframes cssload-spin {
	100%{ transform: rotate(360deg); }
}
@keyframes cssload-spin {
	100%{ transform: rotate(360deg); }
}
